.is-hidden {
  display: none;
}

img {
  height: 400px;
  margin-right: 15px;
}

.cat-info {
  margin-top: 20px;
  display: flex;
}

.loader {
  letter-spacing: 2px;
  font-size: 30px;
  position: relative;
}

.loader:before {
  content: "Loading";
  color: #0c0c0c;
}

.loader:after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #ff3d00;
  border-radius: 50%;
  margin: auto;
  animation: motion 3s ease-in-out infinite;
  position: absolute;
  top: 30px;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes motion {
  0%, 50%, 100% {
    transform: translateX(0)scale(1);
  }

  25% {
    transform: translateX(-100px)scale(.3);
  }

  75% {
    transform: translateX(100px)scale(.3);
  }
}

